:root {
  --primary: #b22222;
  --dark: #131a26;
  --light: #eee;
  
}

.App {
  height: 100vh;
  width: 100%;
  background-color: var(--light);
  font-family: 'Fira Sans', san-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

.calculator{
  width: 100%;
  max-width: 600px;
  background-color: #FFF;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
  height: 500px;
}

.display{
  display: flex;
  background-color: var(--dark);
  padding: 16px;
  color: rgb(255, 255, 255);
  height: 141px;
  align-items: center;
  justify-content: flex-end;
  font-size: 30px;
}
.display1{
  color: rgba(255, 255, 255, 0.5);
}
button{
  appearance: none;
  border: none;
  outline: none;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  transition: 0.4s;
}
button:hover{
  opacity: 0.9;
}
.operators{
  display: flex;
  height: 15%;
}
.operators button{
  flex: 1 1 0%;
  background-color: var(--primary);
  font-weight: 700;
}
.digits{
  display: flex;
  flex-wrap: wrap;
  height: 50%;
}
.digits button{
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: var(--dark);
}

